import {
  Component,
  Directive,
  ViewEncapsulation,
  ChangeDetectionStrategy,
} from '@angular/core';

import {VdlIconRegistry} from '../icon/icon-registry';

@Directive({
  selector: '[vdl-top-bar-logo]',
  host: {
    '[class.vdl-top-bar-logo]': 'true'
  }
})
export class VdlTopBarLogo {

}

@Directive({
  selector: '[vdl-top-bar-title]',
  host: {
    '[class.vdl-top-bar-title]': 'true'
  }
})
export class VdlTopBarTitle {

}

@Directive({
  selector: 'vdl-top-bar-actions',
  host: {
    '[class.vdl-top-bar-actions]': 'true'
  }
})
export class VdlTopBarActions {

}

@Component({
  moduleId: module.id,
  selector: 'vdl-top-bar',
  templateUrl: 'top-bar.html',
  styleUrls: ['top-bar.css'],
  host: {
    '[class.vdl-top-bar]': 'true'
  },
  viewProviders: [VdlIconRegistry],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class VdlTopBar {
}
